const ProductDetails = ({ product, height }) => {
  const images = [...product.images];
  const { width } = useSelector((state) => state.dimensions);
  const breakpoint = 700;
  const [currentPic, setCurrentPic] = useState({});

  const changePic = (index) => {
    setCurrentPic({
      pic: images[index],
      index: index,
    });
  };

  useEffect(() => {
    setCurrentPic({
      pic: images[0],
      index: 0,
    });
  }, []);

  return (
    <div
      className={`productdetails 
      ${!product.pulltest ? "productdetails--nogauge" : ""} 
      // ${product.numVariants === 1 ? "productdetails--oneVariant" : ""}
      ${product.noMainPicture ? "productdetails--noMainPicture" : ""}
      `}>
      {!product.noMainPicture && (
        <div className='productdetails__image'>
          <Figure
            image={currentPic.pic}
            alt={product.name}
            animate
            height='30rem'
          />
          <div className='featured__image-selector fishfacts__image-selector productdetails__image-selector'>
            {images.length > 1 &&
              images.map((_, index) => {
                return (
                  <div
                    key={index}
                    onClick={() => changePic(index)}
                    className={
                      index === currentPic.index
                        ? "featured__image-selector-dot featured__image-selector-dot--active"
                        : "featured__image-selector-dot"
                    }
                  />
                );
              })}
          </div>
        </div>
      )}
      <div className='productdetails__details'>
        <p className='productdetails__name'>{product.name}</p>
        <p className='productdetails__maker'>
          <span>by </span>
          <Link to={`/brands/${product.brand}`}>{product.brand}</Link>
        </p>

        <span className='productdetails__rating'>
          <Rating
            value={product.rating}
            color={"#e0dfa7"}
            text={`${product.numReviews} reviews`}
          />{" "}
        </span>
        <p className='productdetails__catchphrase'>{product.catchPhrase}</p>
      </div>
      <div
        className='productdetails__description'
        dangerouslySetInnerHTML={{ __html: product.description }}></div>

      {product.numVariants === 1 && (
        <p className='productdetails__price'>
          Price: ${product.variants[0].price}
        </p>
      )}

      {product.pulltest &&
        (width > breakpoint ? (
          <PullTestGauge test={product.pulltest} />
        ) : (
          <HorizontalPullTestGauge test={product.pulltest} />
        ))}
    </div>
  );
};

export default ProductDetails;
